.projects {
  @include header-top-padding();

  nav {
    width: 100%;
    ul {
      width: 100%;
      display: flex;
      justify-content: space-between;
      padding: 30px 0;
      li {
        @include size-menu();
      }
    }
  }

  // .flex__header, .flex__item
  div {
    width: 100%;
    padding: 5px 0;
    border-bottom: solid 0.5px $light-line;
    display: flex;
  }

  &__header {
    @include breakpoint("<=medium") {
      display: none!important;
    }
    h1 {
      @include size-menu();
      padding: 30px 0;
      flex-basis: 100%;
      color: grey;
      font-weight: 300;
    }
    p {
      @include size-menu();
      flex-basis: 20%;
      padding: 30px 0;
    }
  }
  &__image {
    @include breakpoint(">medium") {
      display: none!important;
    }
  }
  &__item {
    @include breakpoint("<=medium") {
      justify-content: space-between;
    }
    h2 {
      @include breakpoint("<=medium") {
        color: black;
        font-size: 20px;
        line-height: 26px;
      }
      @include breakpoint(">medium") {
        @include size-header();
        font-weight: 500;
        flex-basis: 100%;

        a {
          color: black;
          text-decoration: none;
          &:hover {
            -webkit-text-stroke: 1px #000;
            color: transparent;
          }
        }
      }
    }
    p {
      @include breakpoint("<=medium") {
        color: black;
        font-size: 20px;
      }
      @include breakpoint(">medium") {
        @include size-header();
        flex-basis: 20%;
        -webkit-text-stroke: 1px #000;
        color: transparent;
      }
    }
  }
}
