.people {
  @include header-top-padding();
  .sub-header ul {
    justify-content: flex-start;
    li {
      margin-right: 15px;
    }
  }

  article {
    display: flex;
    flex-wrap: wrap;

    .member {
      flex-basis: 30%;
      padding-bottom: 20px;
      img {
        width: 80%;
        padding-right: 10px;
        padding-bottom: 10px;
      }
    }

    @include breakpoint("<=medium") {
      // 768
      .member {
        flex-basis: 50%;
      }
    }
  }
}
