.about {
  @include header-top-padding();

  article {
    margin-bottom: 80px;

    h4 {
      margin-bottom: 20px;
    }
  }

  &__top {
    width: 100%;
    padding: 60px 0;
  }

  &__description {
    margin: 60px 0;
    display: flex;
    justify-content: start;
    p {
      flex-basis: 60%;
      margin-right: 60px;
    }
    a {
      font-size: 20px;
      color: #c3c3c3;
    }
  }

  &__awards {
    width: 100%;
    padding: 5px 0;
    border-bottom: solid 0.5px $light-line;
    display: flex;
    justify-content: flex-start;

    p {
      &:nth-of-type(1) {
        flex-basis: 20%;
      }
      &:nth-of-type(2) {
        flex-basis: 50%;
      }
      &:nth-of-type(3) {
        flex-basis: 30%;
      }
    }
  }
  &__awards__header {
    p {
      padding: 30px 0;
    }
  }

  &__awards__item {
    p {
      -webkit-text-stroke: 1px #000;
      color: transparent;
      padding: 15px 0;
    }
  }

  .social_media {
    ul {
      padding: 20px 0;
      li {
        a {
        }
      }
    }
  }
}
