/* Header */

header {
  width: 100vw;
  text-align: center;
  position: fixed;
  z-index: 10000;
  backdrop-filter: blur(1px);

  // for sp nav menu background
  transition: background-color 0.5s ease-in-out;

  .container {
    z-index: 10000;
    display: flex;
    justify-content: space-between;
    background-color: transparent;
    @include header-height();
    padding-bottom: 10px;
    border-bottom: solid 0.5px black;
  }

  .logo {
    display: block;
    margin-right: auto;

    img,
    svg {
      width: auto;
      height: 50px;
      margin: 10px 0 0;
      @include breakpoint("<=small") {
        // under 480px
        height: 30px;
      }
      @include breakpoint(">=xlarge") {
        width: 204px;
        height: auto;
        margin: 35px 0 0;
      }
      path:not(.logo-s):not(.logo-f) {
        transition: ease-in 0.6s;
      }
      path.logo-f {
        transition: 0.4s;
        transition-delay: 0.1s;
      }
    }
    &:hover,
    &.scrolled {
      svg {
        path:not(.logo-s):not(.logo-f) {
          transition: 0.5s;
          opacity: 0;
        }
        path.logo-f {
          transition: 0.5s;
          transition-delay: 0.2s;
          transform: translate(-106.5px, -16px) !important;
        }
      }
    }
  }

  nav {
    ul {
      display: flex;
      li {
        @include header-line-height();
        font-family: $MainFontFamily;
        font-size: 20px;
        font-weight: 500;
        text-align: left;
        color: #000000;
        margin: 0 10px;

        @include breakpoint(">=xlarge") {
          font-size: 30px;
        }

        a {
          @include header-line-height();
          text-decoration: none;
          color: black;
          -webkit-text-stroke: white 0.5px;
          &:hover {
            text-decoration: underline;
          }
        }
      }
    }

    @include breakpoint("<=medium") {
      // ハンバーガーメニュー項目
      position: absolute;
      display: flex;
      position: absolute;
      width: 100vw;
      height: 100vh;

      transition: background-color 0.5s ease-in-out;
      background-color: transparent;

      // ヘッダー分ずらす
      top: $header-height;
      @include breakpoint("<=small") {
        top: $header-height-sp;
      }
      // 隠す
      left: -100vw;

      ul {
        flex-direction: column;
        justify-content: flex-start;
        margin-top: 35px;
        li {
          line-height: 35px;
          margin-left: 30px;

          transition: opacity 0.5s ease-in-out;
          opacity: 0;

          @include breakpoint("<=small") {
            margin-left: 15px;
          }
          a {
            line-height: inherit;
            color: white;
          }
        }
      }
    }
  }

  nav.clicked {
    // メニューの表示
    left: 0;
    border-top: solid 1px white;
    background-color: rgba(0, 0, 0, 0.8);
    ul {
      li {
        opacity: 1;
      }
    }
  }

  button {
    display: none;
    @include header-height();
    @include breakpoint("<=medium") {
      display: block;
      font-size: 24px;
      @include breakpoint("<=small") {
        display: block;
        font-size: 16px;
      }
    }
  }
}

header.blackedOut {
  // メニュー表示中のヘッダー部分
  background-color: rgba(0, 0, 0, 0.8);
  .logo svg {
    fill: white;
  }
  #menuButton {
    color: white;
  }
}
