.process {
  @include header-top-padding();

  article {
    margin: 60px 0;
    width: 60%;
    @include breakpoint("<=medium") {
      width: 100%;
      margin: 15px 0 30px;
    }
    max-width: 1000px;

    img {
      max-width: 900px;
    }
    h1 {
      margin-top: 20px;
    }
    h3 {
    }
    p {
    }

    @include breakpoint("xlarge") {
      img {
        margin-left: 100px;
      }
      h1 {
        margin-top: 20px;
        span {
          font-size: 60%;
          padding-right: 60px;
        }
      }
      h3 {
        margin-left: 100px;
      }
      p {
        margin-left: 100px;
      }

      &:nth-of-type(1) {
        margin-left: 0px;
      }
      &:nth-of-type(2) {
        margin-left: 40%;
      }
      &:nth-of-type(3) {
        margin-left: 20%;
        img {
          margin-left: 0px;
        }
      }
      &:nth-of-type(4) {
        margin-left: 100px;
        img {
          margin-left: -100px;
        }
      }
      &:nth-of-type(5) {
        margin-left: 20%;
        img {
          margin-left: 100px;
        }
      }
    }
  }
}
