@import url("https://fonts.googleapis.com/css?family=Source+Sans+Pro:300,600,900,300italic,600italic,900italic");

@font-face {
  font-family: "Aktiv Grotesk";
  src: local("Aktiv Grotesk Medium"), local("Aktiv-Grotesk-Medium"),
    url("/assets/fonts/AktivGroteskCorp-Medium.woff2") format("woff2"),
    url("/assets/fonts/AktivGroteskCorp-Medium.woff") format("woff"),
    url("/assets/fonts/AktivGroteskCorp-Medium.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: "Aktiv Grotesk";
  src: local("Aktiv Grotesk Bold"), local("Aktiv-Grotesk-Bold"),
    url("/assets/fonts/AktivGroteskCorp-Bold.woff2") format("woff2"),
    url("/assets/fonts/AktivGroteskCorp-Bold.woff") format("woff"),
    url("/assets/fonts/AktivGroteskCorp-Bold.ttf") format("truetype");
  font-weight: 700;
  font-style: normal;
}

// font families
$MainFontFamily: "Aktiv Grotesk", Arial, "Hiragino Kaku Gothic ProN",
  "Hiragino Sans", Meiryo, sans-serif;

html {
  * {
    font-family: $MainFontFamily;
    font-weight: 500;
  }

  a {
    color: black;
    text-decoration: none;
    &:hover {
      text-decoration: underline;
    }
  }

  @include breakpoint("<=xlarge") {
  }
}

.post {
  h1 {
    @include breakpoint(">=xlarge") {
      font-size: 80px;
    }
    @include breakpoint("large") {
      font-size: 60px;
    }
    @include breakpoint("medium") {
      font-size: 50px;
    }
    @include breakpoint("small") {
      font-size: 40px;
    }
  }
  h2 {
    @include breakpoint(">=xlarge") {
      font-size: 60px;
    }
    @include breakpoint("large") {
      font-size: 50px;
    }
    @include breakpoint("medium") {
      font-size: 40px;
    }
    @include breakpoint("small") {
      font-size: 30px;
    }
  }
  h3 {
    font-weight: bold;
    @include breakpoint(">=xlarge") {
      font-size: 50px;
    }
    @include breakpoint("large") {
      font-size: 40px;
    }
    @include breakpoint("medium") {
      font-size: 30px;
    }
    @include breakpoint("small") {
      font-size: 20px;
    }
  }
  h4 {
    font-weight: bold;
    @include breakpoint(">=xlarge") {
      font-size: 40px;
    }
    @include breakpoint("large") {
      font-size: 30px;
    }
    @include breakpoint("medium") {
      font-size: 20px;
    }
    @include breakpoint("small") {
      font-size: 20px;
    }
  }
  p,
  li {
    white-space: normal;
    word-break: break-word;

    @include breakpoint(">=xlarge") {
      font-size: 20px;
      line-height: 35px;
    }
    @include breakpoint("large") {
      font-size: 20px;
      line-height: 30px;
    }
    @include breakpoint("medium") {
      font-size: 16px;
      line-height: 20px;
    }
    @include breakpoint("small") {
      font-size: 16px;
      line-height: 18px;
    }
  }
}

.stroke {
  -webkit-text-stroke: 1px #000;
  color: transparent;
}
